<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
    </div>
    <div class='tap'>
      <HorizontalTap class='center' :data='data2' :active="4" style="padding-left: 100px;"></HorizontalTap>
    </div>
    <div class="row mt-5 " >
      <div v-if="pdfFineFlg">
        <!--一个是别人的，一个是我的-->
        <!--<i class="el-icon-error close-btn"  @click="closePdf" ></i>-->
        <PDF v-if="pdfFineFlg" :pdf-url="pdfFineUrl" :is-contrast="pdfFineIsContrast" :display-id="displayFineId"></PDF>
        <PDF v-if="pdfMyFlg" :pdf-url="pdfMyUrl" :is-contrast="pdfMyIsContrast" :display-id="displayMyId" style="margin-left: 50%"></PDF>
      </div>
      <div class="col-md-8 col-md-offset-2 text-center shadow-radius">
        <div class="col-md-12 col-xs-12 text-center">
          <div class="col-md-5 col-xs-12 text-center">
            <img class="growth-img" src="../../../../assets/img/growth.png">
          </div>
          <div class="col-md-7 col-xs-12" v-if="classPercent != null && schoolPercent != null && projectPercent != null">
            <div class="col-md-12 text-center pt-1">
              <h1 class="font-bold project-name">{{projectName}}</h1>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <a :title="'在本项目已超过' + projectPM + '人'"><el-progress type="circle" :percentage="projectPercent" color="rgb(19, 206, 102)" style="margin-left: 5%;margin-right: 5%"></el-progress></a>
              <span class="col-md-12 mt-3 font-small">你在本项目中排名{{rankInfo.projectRank}}/{{rankInfo.projectHeadCount}}</span>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <a :title="'在学校内已超过' + schoolPM + '人'"><el-progress type="circle" :percentage="schoolPercent" color="#e6a23c" style="mrgb(32, 160, 255);argin-left: 5%;margin-right: 5%"></el-progress></a>
              <span class="col-md-12 mt-3 font-small">你在学校中排名{{rankInfo.schoolRank}}/{{rankInfo.schoolHeadCount}}</span>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <a :title="'在班级内已超过' + classPM + '人'"><el-progress type="circle" :percentage="classPercent" color="rgb(32, 160, 255)" style="margin-left: 5%;margin-right: 5%"></el-progress></a>
              <span class="col-md-12 mt-3 font-small">你在班级中排名{{rankInfo.classRank}}/{{rankInfo.classHeadCount}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="mt-7 pl-8 col-xs-12 col-md-offset-2 col-md-8">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in activities"
            :key="index"
            :icon="activity.icon"
            :type="activity.type"
            :color="activity.color"
            :size="activity.size"
            :placement="'top'"
            :timestamp="activity.timestamp">
            <div class="font-bold font-large" style="margin-top: -12px" v-if="activity.dotType === 'stage'">
              <div><a title="该阶段反馈报告" @click="goReport(activity)">{{activity.content}}<i class="iconwendangmoban iconfont  ml-3" style="color: #6612cf;"></i></a> </div>
            </div>
            <div v-else style="margin-top: -25px" >
              <div>
                {{activity.content}}
                <span v-if="activity.status === 0" class="task-status" style="background-color: #409eff">进行中</span>
                <span v-if="activity.status === -1" class="task-status" style="background-color: rgb(255, 73, 73)">未开始</span>
                <span v-if="activity.status === 1" class="task-status" style="background-color: rgb(19, 206, 102)">已完成</span>
                <span v-if="activity.status === 2" class="task-status" style="background-color: rgb(255, 73, 73)">未完成</span>
                <span v-if="activity.dotOpen === false" @click="dotOpenClose(activity, index)"><i class="el-icon-circle-plus-outline"></i></span>
                <span v-if="activity.dotOpen === true" @click="dotOpenClose(activity, index)"><i class="el-icon-remove-outline"></i></span>
              </div>
              <div v-if="activity.dotOpen">
                <!-- 上传成果物阶段-->
                <div v-if="!activity.fineMark" class="row pt-4">
                  <div v-if="!activity.noDataFlg">
                  <!--若果有最优成果物-->
                    <div class="" style="font-size: 13px">
                      <div v-if="activity.canDownFlg">
                        <span>我的成果物：{{activity.outputNameFormat}}</span>
                        <a title="下载"><i class="el-icon-download font-bold action-icon"  v-if="activity.canDownFlg" @click="downLoadOutPut(activity)"></i></a>
                      </div>
                      <div v-else><span>我的成果物：暂无</span></div>
                      <div v-if="activity.fineUserName"><span>最优成果物：{{activity.fineOutputNameFormat}}</span>
                        <span style="margin-left: 10px">{{activity.fineUserName}}</span>
                        <a title="预览"><i class="iconfont iconliulan action-icon" style="font-size: 13px" v-if="activity.canLookFlg" @click="goPdf(activity)"></i></a>
                      </div>
                      <div v-else><span>最优成果物：暂无</span></div>
                    </div>
                  <!--若无最优成果物-->
                  </div>
                </div>
                <!--测试答题阶段-->
                <div v-else class="row pt-4" style="font-size: 13px;">
                  <div><span>我的分数：{{activity.mark}}</span></div>
                  <div><span>最佳成绩：{{activity.fineMarkUserName}}</span><span style="margin-left: 10px">分数：{{activity.fineMark}}</span></div>
                </div>
              </div>
            </div>
          </el-timeline-item>
        </el-timeline>
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import PDF from '../../../../components/learning-library/pdf'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    PDF
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      taskList: null,
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务清单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_self'
      }, {
        name: '成长轨迹',
        url: '',
        type: '_self'
      }],
      activities: [],
      projectName: '',
      pdfFineFlg: false,
      pdfMyFlg: false,
      pdfFineUrl: '',
      pdfMyUrl: '',
      pdfFineIsContrast: false,
      pdfMyIsContrast: false,
      displayFineId: 'the-canvas',
      displayMyId: 'the-canvas-contrast',
      rankInfo: null,
      classPM: null,
      projectPM: null,
      schoolPM: null,
      classPercent: null,
      projectPercent: null,
      schoolPercent: null
    }
  },
  methods: {
    loadData () {
      let me = this
      me.projectName = localStorage.getItem('projectName')
      me.$http.post('/doproject/rankStatisticsInfo', { 'teamId': localStorage.getItem('teamId') }, res => {
        if (res.success) {
          console.log(res)
          me.rankInfo = res.data
          me.rankInfo.classRank = me.rankInfo.classRank == null ? me.rankInfo.classHeadCount : me.rankInfo.classRank
          me.rankInfo.projectRank = me.rankInfo.projectRank == null ? me.rankInfo.projectHeadCount : me.rankInfo.projectRank
          me.rankInfo.schoolRank = me.rankInfo.schoolRank == null ? me.rankInfo.schoolHeadCount : me.rankInfo.schoolRank
          me.classPM = me.rankInfo.classHeadCount - me.rankInfo.classRank
          me.projectPM = me.rankInfo.projectHeadCount - me.rankInfo.projectRank
          me.schoolPM = me.rankInfo.schoolHeadCount - me.rankInfo.schoolRank
          me.projectPercent = Math.floor((me.projectPM / me.rankInfo.projectHeadCount) * 100)
          me.schoolPercent = Math.floor((me.schoolPM / me.rankInfo.schoolHeadCount) * 100)
          me.classPercent = Math.floor((me.classPM / me.rankInfo.classHeadCount) * 100)
          me.$http.post('/doproject/growthTrajectoryInfo', {
            'teamId': localStorage.getItem('teamId')
          }, resgrowth => {
            if (resgrowth.success) {
              me.taskList = resgrowth.data
              me.formatData()
            }
          })
        }
      })
    },
    formatData () {
      let obj = {}
      this.activities = []
      for (var i = 0; i < this.taskList.length; i++) {
        obj = { content: this.taskList[i].stageName, timestamp: '', color: '#FF6600', size: 'large', icon: '', dotType: 'stage', stageId: this.taskList[i].stageId }
        this.activities.push(obj)
        for (var j = 0; j < this.taskList[i].detail.length; j++) {
          obj = this.taskList[i].detail[j]
          if (obj.status === -1 || obj.status === 2) {
            this.formatDot(obj, 'el-icon-close', 'rgb(255, 73, 73)', '')
            if (obj.status === 2) {
              obj.dotOpen = false
            }
          } else if (obj.status === 1 || obj.status === 3) {
            this.formatDot(obj, 'el-icon-check', 'rgb(19, 206, 102)', obj.updateTime)
            obj.dotOpen = false
          } else if (obj.status === 0) {
            this.formatDot(obj, 'el-icon-more', '#409eff', '')
            obj.dotOpen = true
          }
          this.activities.push(obj)
        }
      }
      this.activities.push({ content: '总结报告', timestamp: '', color: '#FF6600', size: 'large', icon: '', dotType: 'stage', stageId: 0 })
    },
    formatDot (obj, icon, color, timestamp) {
      obj.content = obj.stepName
      obj.timestamp = timestamp
      obj.size = 'normal'
      obj.dotType = 'step'
      obj.outputNameFormat = this.formatOutputName(obj.outputName)
      if (obj.outputNameFormat.length > 15) {
        obj.outputNameFormat = obj.outputNameFormat.substr(0, 15) + '...'
      }
      obj.fineOutputNameFormat = this.formatOutputName(obj.fineOutputName)
      if (obj.fineOutputNameFormat.length > 15) {
        obj.fineOutputNameFormat = obj.fineOutputNameFormat.substr(0, 15) + '...'
      }
      obj.icon = icon
      obj.color = color
      if (obj.fineOutputName == null && obj.outputName == null) {
        // 都没有成果物时显示暂无
        obj.isContrast = false
        obj.canLookFlg = false
        obj.noDataFlg = true
        obj.canDownFlg = false
      } else if (obj.fineOutputName !== null && obj.outputName == null) {
        // 有最优的成果物，没有本人的成果物
        obj.isContrast = false
        obj.canLookFlg = true
        obj.noDataFlg = false
        obj.canDownFlg = false
      } else if (obj.fineOutputName == null && obj.outputName !== null) {
        // 无最优的成果物，有本人的成果物
        obj.isContrast = false
        obj.canLookFlg = false
        obj.noDataFlg = false
        obj.canDownFlg = true
      } else if (obj.fineOutputName !== null && obj.outputName !== null) {
        // 有最优的成果物，有本人的成果物
        obj.isContrast = true
        obj.canLookFlg = true
        obj.noDataFlg = false
        obj.canDownFlg = true
      }
    },
    formatOutputName (name) {
      if (name !== null) {
        let outputName = name.split('$_$')
        name = outputName[1]
        return name
      } else {
        return ''
      }
    },
    customColorMethod (percentage) {
      if (percentage < 60) {
        return 'rgb(19, 206, 102)'
      } else if (percentage <= 89 && percentage >= 60) {
        return '#6612cf'
      } else {
        return 'rgb(255, 73, 73)'
      }
    },
    goPdf (obj) {
      let me = this
      this.pdfFineFlg = false
      this.pdfMyFlg = false
      // me.$http.post('/assistant/openpdf', { 'outputName': obj.fineOutputName }, res => {
      //   console.log(res)
      // })
      // return false
      if (obj.isContrast) {
        me.$nextTick(() => {
          this.pdfFineUrl = 'http://www.aimaonline.cn/file/pdffile/' + obj.fineOutputName + '.pdf'
          // this.pdfFineUrl = 'http://39.100.37.131/file/pdffile/' + obj.fineOutputName + '.pdf'
          // this.pdfMyUrl = 'http://39.100.37.131/file/pdffile/' + obj.outputName + '.pdf'
          this.pdfMyUrl = 'http://www.aimaonline.cn/file/pdffile/' + obj.outputName + '.pdf'
          this.pdfFineIsContrast = true
          this.pdfMyIsContrast = true
          this.pdfFineFlg = true
          this.pdfMyFlg = true
        })
      } else {
        // this.pdfFineUrl = 'http://39.100.37.131/file/pdffile/' + obj.fineOutputName + '.pdf'
        this.pdfFineUrl = 'http://www.aimaonline.cn/file/pdffile/' + obj.fineOutputName + '.pdf'
        this.pdfFineIsContrast = false
        this.pdfFineFlg = true
      }
    },
    closePdf () {
      this.pdfFineFlg = false
      this.pdfMyFlg = false
    },
    downLoadOutPut (obj) {
      const loading = Loading.service({
        lock: true,
        text: '下载中请稍候...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)'
      })
      this.$axios({
        method: 'post',
        // url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadFile',
        url: 'http://www.aimaonline.cn:8085/lying-web/doproject/output/downloadFile',
        data: { 'filename': obj.outputName },
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = obj.outputName.split('$_$')[1]
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      ).catch(
        (error) => {
          console.log(error)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      )
    },
    goReport (obj) {
      // 总结报告情况
      if (obj.stageId === 0) {
        if (this.taskList[this.taskList.length-1].detail[0].status !== 1){
          this.$MessageBox.alert('暂无总结报告，请完成所有任务', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          this.$MessageBox.prompt('请填写项目评价', '提示', {
            confirmButtonText: '确定',
            callback: action => {
              window.location = 'student-report.html'
            }
          })
        }
      } else {
        localStorage.setItem('stageId', obj.stageId)
        window.location = 'stage-report.html'
      }
    },
    dotOpenClose (obj, index) {
      let obk = obj
      let pp = !obj.dotOpen
      obk.dotOpen = pp
      this.$set(this.activities, index, obk)
    }
  }
}
</script>
<style scoped lang="scss">
  @media (min-width: 768px) and (max-width: 1400px) {
    .pingfen-mt{
      margin-top: 64px;
    }
  }
  @media (min-width: 1401px) and (max-width: 1601px) {
    .pingfen-mt{
      margin-top: 120px;
    }
    .project-name{
      padding-top: 10px;
    }
  }
  @media (min-width: 1602px) and (max-width: 3000px) {
    .pingfen-mt{
      margin-top: 20%;
    }
    .project-name{
      padding-top: 10%;
    }
  }
  div::-webkit-scrollbar{
    width:10px;
    height:10px;
    /**/
  }
  div::-webkit-scrollbar-track{
    background: #ffffff;
    border-radius:2px;
  }
  div::-webkit-scrollbar-thumb{
    background: #666666;
    border-radius:10px;
    width: 5px;
  }
  div::-webkit-scrollbar-corner{
    background: #ffffff;
  }
  .task-status{
    margin-left: 5px;
    font-size: 8px;
    font-weight: bold;
    height: 20px;
    line-height: 20px;
    color: white;
    border-radius: 15px;
    padding: 2px;
  }
  .rank-img{
    border-radius: 50%;
    width:100px;
    height:100px;
    margin: 0px auto;
    background-size: contain;
    .rank-user{
      color:rgba(255,255,255,1);
      width:100%;
      height:100%;
      background-color: #000000;
      border-radius:50%;
      opacity:0.68;
      display:none;
      z-index:101;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      padding-top: 42px;
      cursor:pointer;
    }
  }
  .rank-img:hover{
    .rank-user{
      display: block;
    }
  }
  .action-icon{
    cursor: pointer;
    margin-left: 10px;
    font-size: 14px;
  }
  .growth-img{
    width:100%;
    height:100%;
    cursor:pointer;
  }
  .mark-per{
    font-size: 50px;
    font-weight: bold;
  }
  .project-name{
    background-image:-webkit-linear-gradient(left, white, #c543d8, #6612cf,white);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
  }
  .close-btn{
    z-index: 1001;
    position: fixed;
    top: 3%;
    left: 95%;
    color: red;
    font-size: 30px;
  }
</style>
<style>
  .el-timeline-item__timestamp {
    color: #909399;
    line-height: 1;
    font-size: 13px;
    position: absolute;
    left: -18%;
    /*top: 5%;*/
  }
  .el-timeline-item {
    position: relative;
    padding-bottom: 80px!important;
  }
  .el-timeline-item__node--large {
    left: -4px!important;
    width: 18px!important;
    height: 18px!important;
  }
  .el-timeline-item__tail {
    position: absolute;
    left: 4px;
    height: 100%;
    border-left: 3px solid #E4E7ED!important;
  }
</style>
